.onHover {
    position: absolute;
    background-color: #ffffff;
    opacity: 0.8;
    filter: alpha(opacity=50);
    -webkit-filter: drop-shadow(0 1px 4px rgba(0,0,0,0.2));
    filter: drop-shadow(0 1px 4px rgba(0,0,0,0.2));
    padding: 3px;
    border-radius: 10px;
    border: 1px solid #cccccc;
    bottom: 12px;
    height: 43px;
}

.onClick {
    position: absolute;
    background-color: white;
    opacity: 0.8;
    filter: alpha(opacity=50);
    -webkit-filter: drop-shadow(0 1px 4px rgba(0,0,0,0.2));
    filter: drop-shadow(0 1px 4px rgba(0,0,0,0.2));
    padding: 5px;
    border-radius: 10px;
    border: 1px solid #cccccc;
    bottom: 12px;
    height: 420px;
    top: 15px;
    /*margin-left: 50%;*/
}

.onClick p{
    margin: 0px;
}
.hide {
    display: none;
}

.show li{ list-style: none; }
.show a { border: 0 !important; text-decoration: none; }
.show a:hover { text-decoration: underline !important; }

#stName {
    float: left;
    padding: 5px;
}

#stImage{
    float: left;
}

#stImageOnClick{
    padding: 3px;
    float: left;
}

#pointermove p{
    margin: 0px;
    margin-left:5px;
}

p, a {
    font-size: 75%;
    color: #000000;
  }

.mainRow{
    float: left;
}
